import { newSpecPage } from '@stencil/core/testing';
import { PrestoVideo } from '../presto-video';

describe('presto-video', () => {
  it('renders', async () => {
    const page = await newSpecPage({
      components: [PrestoVideo],
      html: `<presto-video src="https://example.com/test.mp4"></presto-video>`,
    });
    expect(page.root).toEqualHtml(`
    <presto-video src="https://example.com/test.mp4">
      <video class="presto-player__player" part="video">
        <source src="https://example.com/test.mp4">
      </video>
    </presto-video>
    `);
  });
});
